<template>
  <section id="user">
    <div class="homeHeader">
      <div class="headerLeft">
        <img :src="QR" alt="" class="userImg">
      </div>
      <div class="headerCenter">
        <div class="userTitle"><span>小布</span><span>{{id}}</span></div>
        <div class="userMoney">{{money}}</div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'user',
  data () {
    return {
      QR: require('../assets/images/game4.png'), // 用户头像
      money: 0, // 用户金额
      id: 353 // 用户id
    }
  }
}
</script>

<style scoped>
   #user{display: inline-block; width: 100%;}
  /*头部样式*/
  .homeHeader{width: 85%;height: 8rem;background: url("./../assets/images/game2.png") no-repeat;background-size: 100% 100%;float: left;}
  .headerLeft{width: 4rem; text-align: center; float: left;height: 4rem;margin-top:1.85rem; margin-left: 6%; background: url("./../assets/images/game3.png") no-repeat;background-size: 100% 100%;}
  .userImg{width: 100%; height: 100%;border-radius: 6px;border: 1.5px solid #fff;}
  .headerCenter{float: left;height: 4.5rem;margin-left: 0.6rem;margin-top: 1.85rem; font-size: 14px;}
  .userTitle{margin: 0.5rem 0;color: #fff;}
  .userTitle span{padding-left: 0.2rem;}
  .userMoney{width: 10rem; color: #fff; text-indent: 2rem; line-height: 1.6rem; height: 1.6rem;background: url("./../assets/images/game5.png") no-repeat;background-size: 100% 100%;}
</style>



