import Vue from 'vue'
import Router from 'vue-router'
import wallet from './../page/wallet' // 钱包页面
import record from './../page/record' // 记录页面
import home from './../page/home' // 首页页面
import service from './../page/service' // 客服页面
import userData from '../page/userData' // 资料页面

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/wallet',
      name: 'wallet',
      component: wallet
    }, // 钱包页面
    {
      path: '/record',
      name: 'record',
      component: record
    }, // 记录页面
    {
      path: '/',
      name: 'home',
      component: home
    }, // 首页页面
    {
      path: '/service',
      name: 'service',
      component: service
    }, // 客服页面
    {
      path: '/userData',
      name: 'userData',
      component: userData
    } // 资料页面
  ]
  //mode: 'history' // 干掉地址栏里边的#号键
})



