<template>
  <section id="home" :style="{backgroundImage:'url('+bg+')'}">
     <!--用户信息-->
     <user></user>
     <!--游戏专区-->
     <div class="centerTxt">
       <div class="textLeft"><img src="./../assets/images/game7.png" alt=""></div>
       <div class="textRight"><img src="./../assets/images/game6.png" alt=""></div>
     </div>
     <!--客服人物-->
     <div class="serverImg">
       <img src="./../assets/images/game8.png" alt="">
     </div>
     <!-- 底部导航菜单 -->
     <tabs :selected="selected"></tabs>
  </section>
</template>

<script>
import tabs from './../components/tabs'
import user from './../components/user'
export default {
  name: 'home',
  data () {
    return {
      selected: 'home', // 导航当前页的状态
      bg: require('./../assets/images/game1.png') // 背景图片
    }
  },
  components: {
    tabs, user
  }
}
</script>

<style scoped >
#home{background-size: 100% 100%;height: 100%;position: fixed;width: 100%;}

/*内容样式  */
.centerTxt{clear: both; display: inline-block; width: 100%;}
.centerTxt .textLeft{float: left;padding-left: 1.5rem;}
.centerTxt .textLeft img{width: 9rem;}
.centerTxt .textRight{float: right;}
.centerTxt .textRight img{width: 9rem;padding-right: 1.5rem;}

/*客服头像样式*/
.serverImg{position: fixed; left: 1rem; bottom: 1.6rem; width: 13rem;}
.serverImg img{width: 100%;}
</style>



