<template>
  <div id="record" :style="{backgroundImage:'url('+bg+')'}">
    <!--用户信息-->
    <user></user>
    <mt-navbar v-model="activeTab" class="navTab">
      <mt-tab-item id="1" class="sizeTxt">北京赛车</mt-tab-item>
      <mt-tab-item id="2" class="sizeTxt">幸运飞艇</mt-tab-item>
    </mt-navbar>
    <mt-tab-container v-model="activeTab" class="details">
      <mt-tab-container-item id="1">
        <div class="tabCont">
            <table class="tabHeader">
                <thead>
                    <tr>
                      <th width="12%">期号</th>
                      <th width="45%">出号</th>
                      <th width="18%">冠亚和</th>
                      <th width="25%">1-5名龙虎</th>
                    </tr>
                </thead>
            </table>
            <table class="tabNr">
                <tbody>
                    <tr v-for="(item,index) in airshipData" :key="index">
                      <td width="12%">{{item.itemId}}</td>
                      <td width="45%">
                        <img v-for="(item2,ind) in item.outputSrc" :key="ind"  class="number" :src="item2"/>
                      </td>
                      <td width="18%">
                        <img v-for="(item2,ind) in item.sizeSrc" :key="ind"  class="number" :src="item2"/>
                      </td>
                      <td width="25%">
                        <img v-for="(item2,ind) in item.dragonSrc" :key="ind"  class="number" :src="item2"/>
                      </td>
                    </tr>
                </tbody>
            </table>
        </div>
      </mt-tab-container-item>
      <mt-tab-container-item id="2">
          <div class="tabCont">
            <table class="tabHeader">
              <thead>
              <tr>
                <th width="12%">期号</th>
                <th width="45%">出号</th>
                <th width="18%">冠亚和</th>
                <th width="25%">1-5名龙虎</th>
              </tr>
              </thead>
            </table>
            <table class="tabNr">
              <tbody>
              <tr v-for="(item,index) in airshipData" :key="index">
                <td width="12%">{{item.itemId}}</td>
                <td width="45%">
                  <img v-for="(item2,ind) in item.outputSrc" :key="ind"  class="number" :src="item2"/>
                </td>
                <td width="18%">
                  <img v-for="(item2,ind) in item.sizeSrc" :key="ind"  class="number" :src="item2"/>
                </td>
                <td width="25%">
                  <img v-for="(item2,ind) in item.dragonSrc" :key="ind"  class="number" :src="item2"/>
                </td>
              </tr>
              </tbody>
            </table>
          </div>
      </mt-tab-container-item>
    </mt-tab-container>
    <!-- 底部导航菜单 -->
    <tabs :selected="selected"></tabs>
  </div>
</template>

<script>
import tabs from './../components/tabs'
import user from './../components/user'
export default {
  name: 'record',
  created () {
    // 北京赛车
    let itemId = 730995
    for (let i = 0; i < 40; i++) {
      this.airshipData[i] = {}
      itemId++
      this.airshipData[i].itemId = itemId
      let outputSrc = []
      for (let i = 0; i < 10; i++) {
        switch (Math.floor(Math.random() * 10)) {
          case 0 :
            outputSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/s0.png?sign=46742e53a5d72cbcf70588ea267ddfbd&t=1553008214')
            break
          case 1 :
            outputSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/s1.png?sign=f915ff9b64ebbdd479d9de24d083a5b1&t=1553008263')
            break
          case 2 :
            outputSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/s2.png?sign=fc785e28acd6974ee6e28abfa2816fa2&t=1553008277')
            break
          case 3 :
            outputSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/s3.png?sign=620b9c2fbfca58d87c4b99a920dfdf88&t=1553008288')
            break
          case 4 :
            outputSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/s4.png?sign=005aed2ac128de47275c46d740a729c3&t=1553008300')
            break
          case 5 :
            outputSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/s5.png?sign=ef9ed1e79f849e9bc57342df72e07e67&t=1553008312')
            break
          case 6 :
            outputSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/s6.png?sign=376de76f8bf96d1b8d623568a8676950&t=1553008324')
            break
          case 7 :
            outputSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/s7.png?sign=4c383d02f8b78ed7a48e0de25d7f53b0&t=1553008335')
            break
          case 8 :
            outputSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/s8.png?sign=8fe9fe86a868272d1b86219a3096665a&t=1553008345')
            break
          case 9 :
            outputSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/s9.png?sign=1dc2229e234a7cfb7f170fdf8e129a08&t=1553008357')
            break
        }
      }
      this.airshipData[i].outputSrc = outputSrc
      // 大小单双
      let sizeSrc = []
      for (let i = 0; i < 4; i++) {
        if (i === 0) {
          switch (Math.floor(Math.random() * 10)) {
            case 0 :
              sizeSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/s0.png?sign=46742e53a5d72cbcf70588ea267ddfbd&t=1553008214')
              break
            case 1 :
              sizeSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/s1.png?sign=f915ff9b64ebbdd479d9de24d083a5b1&t=1553008263')
              break
            case 2 :
              sizeSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/s2.png?sign=fc785e28acd6974ee6e28abfa2816fa2&t=1553008277')
              break
            case 3 :
              sizeSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/s3.png?sign=620b9c2fbfca58d87c4b99a920dfdf88&t=1553008288')
              break
            case 4 :
              sizeSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/s4.png?sign=005aed2ac128de47275c46d740a729c3&t=1553008300')
              break
            case 5 :
              sizeSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/s5.png?sign=ef9ed1e79f849e9bc57342df72e07e67&t=1553008312')
              break
            case 6 :
              sizeSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/s6.png?sign=376de76f8bf96d1b8d623568a8676950&t=1553008324')
              break
            case 7 :
              sizeSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/s7.png?sign=4c383d02f8b78ed7a48e0de25d7f53b0&t=1553008335')
              break
            case 8 :
              sizeSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/s8.png?sign=8fe9fe86a868272d1b86219a3096665a&t=1553008345')
              break
            case 9 :
              sizeSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/s9.png?sign=1dc2229e234a7cfb7f170fdf8e129a08&t=1553008357')
              break
          }
        } else {
          switch (Math.floor(Math.random() * 4)) {
            case 0 :
              sizeSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/da.png?sign=abc733ba81ea79ba31d08d45f4d2abd1&t=1553008645')
              break
            case 1 :
              sizeSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/xiao.png?sign=01dcd39bf2f2221cc7e6acc534a20218&t=1553008668')
              break
            case 2 :
              sizeSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/dan.png?sign=41ffe1a4ee9a3c98a6b90d2caeaeb377&t=1553008656')
              break
            case 3 :
              sizeSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/shuang.png?sign=a6b872961ffc2a85b42daa6249466840&t=1553008680')
              break
          }
        }
      }
      this.airshipData[i].sizeSrc = sizeSrc
      let dragonSrc = []
      for (let i = 0; i < 5; i++) {
        switch (Math.floor(Math.random() * 4)) {
          case 0 :
            dragonSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/long.png?sign=14ab49d954adc6bcdab8943d54fdc78a&t=1553008929')
            break
          case 1 :
            dragonSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/hu.png?sign=a352670e446bf0de9c6c2e8d52516ee3&t=1553008989')
            break
          case 2 :
            dragonSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/long.png?sign=14ab49d954adc6bcdab8943d54fdc78a&t=1553008929')
            break
          case 3 :
            dragonSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/hu.png?sign=a352670e446bf0de9c6c2e8d52516ee3&t=1553008989')
            break
          case 4 :
            dragonSrc.push('https://6775-guide-619e01-1258811373.tcb.qcloud.la/hu.png?sign=a352670e446bf0de9c6c2e8d52516ee3&t=1553008989')
            break
        }
      }
      this.airshipData[i].dragonSrc = dragonSrc
    }
  },
  data () {
    return {
      selected: 'record', // 导航当前页的状态
      activeTab: '1', // 默认选中
      airshipData: [], // 北京赛车数据
      bg: require('./../assets/images/game21.png') // 背景图片
    }
  },
  components: {
    tabs, user
  }
}
</script>

<style scoped>
  #record{overflow-y: scroll !important; background-size: 100% 100%;height: 100%;position: fixed;width: 100%;}

  /*北京赛车和幸运飞艇切换样式*/
  .navTab{height: 2rem; width: 54%; margin-left: 5.5%; border: 1px solid #f44360; border-bottom: 0; line-height: 2rem;}
  .tabCont{border: 1px solid #f44360;position: fixed;overflow: auto; height: 62%; border-bottom: none; margin-left:5.5%; width: 89%;  font-size: 12px;color: #fff; text-align: center;}
  .tabHeader{position: fixed ;background: #321311; border-bottom: 1px solid #f44360;margin-top: -0.09rem;border-collapse:collapse; width: 88.8%; font-size: 12px;color: #fff; text-align: center;}
  .tabHeader tr th{font-size: 0.7rem;padding: 0.2rem 0; border-right:1px solid #f44360; }
  .tabHeader tr th:last-child{border-right: none;}
  .tabNr{border-collapse:collapse; width: 100%;margin-top: 1.3rem; overflow-y: scroll; font-size: 12px;color: #fff; text-align: center;}
  .tabNr tr{border-bottom: 0.05rem solid #7c7c7c;}
  .tabNr tr:last-child{border-bottom: none;}
  .tabNr tr td{ font-size: 0.7rem;}
  .tabNr tr td img{ display: inline-block; width: 0.8rem; padding:0 0.04rem;margin-top: 0.2rem;}
  @media only screen and (max-width: 320px){
    .tabHeader{margin-top: 0;}
    .tabNr{margin-top: 0.2rem;}
  }

</style>



